
.border {
    border: 5px solid black;
}
.bc {
    background: rgba(247, 245, 245, 0.5);
}
.br {
    border-radius: 10px;
}
.br-5 {
    border-radius: 5px;
    position: relative;
    right: 9.5rem;
}
.buttons {
    font-size: 3rem;
}
.remove {
    color: red;
}
.p-ilb {
    display: inline-block;
}
.fixed {
    position: fixed;
}
.overflow {
    overflow: auto;
}
.overflow-h {
    overflow: hidden;
    white-space: nowrap;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}
.move-left {
    position: relative;
    right: .5px;
    transition: right 10s linear;
}
.move-left:hover {
    right: 700px;
}
.playlist-area {
    max-height: 100vh;
    max-width: 25vw;
}
::-webkit-scrollbar {
    width: 5px;
}
::-webkit-scrollbar-track {
    background: #f1f1f1;
}
::-webkit-scrollbar-thumb {
    background: #888;
}
.playlist-font {
    font-size: 3rem;
}
.m-l {
    margin-left: 10rem;
}
.floater {
    float: right;
}



